.container-content {
	box-sizing: border-box;
	margin-left: auto;
	margin-right: auto;
	max-width: 825px;
	padding-left: 20px;
	padding-right: 20px;
	width: auto;
	font-family: $container-content-font;
	font-weight: 400;
	.block-content {
		&__title {
			display: block;
			text-align: center;
			font-family: 'DKLemomYellowSun','Source Sans Pro','Helvetica Neue',Helvetica,Arial,sans-serif;
			line-height: 1.2;
			color: #a1c856;
			font-size: 2.8rem;
			@include rmin(768) {
				font-size:  4.4rem;
				margin-bottom:  15px; } }
		&__main-content {
			&_wrapper {
				padding-top: 15px;
				padding-bottom: 25px;
				@include rmin(768) {
					padding-top:  25px;
					padding-bottom: 50px; }
				border-top: 1px solid #d6d6d6;
				position: relative;
				&::before {
					content: '';
					width: 100%;
					height: 1px;
					background-color: #d6d6d6;
					position: absolute;
					top: 2px;
					left: 0;
					right: 0; } }
			&_cols {
				display: flex; }

			&_cols-wrapper {
				width: 50%;
				&:nth-child(odd) {
					padding-right: 15px; }
				&:nth-child(even) {
					padding-left: 15px; } }
			&_cols-link {
				&:nth-child(even) {
					padding-left: 100px;
					font-size: 1.2rem;
					position: relative;
					&::before {
						content: '';
						width: 2px;
						height: 75px;
						background: #a1c856;
						position: absolute;
						left: 100px;
						top: 75px; }
					a {
						display: block;
						color: #a1c856;
						padding-left: 20px;
						margin-bottom: 15px;
						&:hover {
							text-decoration: none; } } } }

			&_margin-img {
				margin-bottom: 90px; }
			&_img-wrapper {
				position: relative;
				img {
					width: calc(100% + 40px);
					margin-bottom: 5px;
					margin-left: -20px;
					margin-right: -20px; } }
			&_text-abs {
				max-width: 250px;
				font-size: 2.0rem;
				color: #ffffff;
				position: absolute;
				bottom: 40px;
				font-weight: 900;
				@include rmin(768) {
					font-size: 4.3rem; } }
			&_main-title {
				font-size: 1.4rem;
				font-weight: 600;
				text-transform: uppercase;
				padding-bottom: 15px;
				margin-bottom: 15px;
				position: relative;
				line-height: 2rem;
				&::before {
					content: '';
					width: 10%;
					height: 3px;
					background: #a1c856;
					position: absolute;
					bottom: 0;
					left: 0; }
				@include rmin(768) {
					font-size: 2.0rem;
					margin-bottom: 30px; } }
			&_text {
				img {
					display: block;
					&:nth-of-type(odd) {
						width: 100%;
						margin: 0 auto 15px;
						@include rmin(600) {
							float: left;
							padding-right: 25px;
							margin-left: -20px;
							width: 50%; } }
					&:nth-of-type(even) {
						float: right;
						width: 50%;
						@include rmin(600) {
							float: left;
							padding-left: 25px;
							margin-right: -20px; } } }
				p {
					font-size: 1.2rem;
					margin-bottom: 15px;
					&:last-child {
						margin-bottom: 65px; }
					a {
						color: #a1c856;
						text-decoration: underline;
						&:hover {
							text-decoration: none; } } } } } } }
