html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: DKLemomYellowSun;
  src: url("../fonts/DKLemonYellowSun.eot") format("eot");
  src: url("../fonts/DKLemonYellowSun.woff") format("woff"), url("../fonts/DKLemonYellowSun.eot") format("eot"), url("../fonts/DKLemonYellowSun.ttf") format("ttf");
  font-weight: 400;
  font-style: normal;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.container-content {
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
  max-width: 825px;
  padding-left: 20px;
  padding-right: 20px;
  width: auto;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 400;
}

.container-content .block-content__title {
  display: block;
  text-align: center;
  font-family: 'DKLemomYellowSun','Source Sans Pro','Helvetica Neue',Helvetica,Arial,sans-serif;
  line-height: 1.2;
  color: #a1c856;
  font-size: 2.8rem;
}

@media only screen and (min-width: 768px) {
  .container-content .block-content__title {
    font-size: 4.4rem;
    margin-bottom: 15px;
  }
}

.container-content .block-content__main-content_wrapper {
  padding-top: 15px;
  padding-bottom: 25px;
  border-top: 1px solid #d6d6d6;
  position: relative;
}

@media only screen and (min-width: 768px) {
  .container-content .block-content__main-content_wrapper {
    padding-top: 25px;
    padding-bottom: 50px;
  }
}

.container-content .block-content__main-content_wrapper::before {
  content: '';
  width: 100%;
  height: 1px;
  background-color: #d6d6d6;
  position: absolute;
  top: 2px;
  left: 0;
  right: 0;
}

.container-content .block-content__main-content_cols {
  display: flex;
}

.container-content .block-content__main-content_cols-wrapper {
  width: 50%;
}

.container-content .block-content__main-content_cols-wrapper:nth-child(odd) {
  padding-right: 15px;
}

.container-content .block-content__main-content_cols-wrapper:nth-child(even) {
  padding-left: 15px;
}

.container-content .block-content__main-content_cols-link:nth-child(even) {
  padding-left: 100px;
  font-size: 1.2rem;
  position: relative;
}

.container-content .block-content__main-content_cols-link:nth-child(even)::before {
  content: '';
  width: 2px;
  height: 75px;
  background: #a1c856;
  position: absolute;
  left: 100px;
  top: 75px;
}

.container-content .block-content__main-content_cols-link:nth-child(even) a {
  display: block;
  color: #a1c856;
  padding-left: 20px;
  margin-bottom: 15px;
}

.container-content .block-content__main-content_cols-link:nth-child(even) a:hover {
  text-decoration: none;
}

.container-content .block-content__main-content_margin-img {
  margin-bottom: 90px;
}

.container-content .block-content__main-content_img-wrapper {
  position: relative;
}

.container-content .block-content__main-content_img-wrapper img {
  width: calc(100% + 40px);
  margin-bottom: 5px;
  margin-left: -20px;
  margin-right: -20px;
}

.container-content .block-content__main-content_text-abs {
  max-width: 250px;
  font-size: 2.0rem;
  color: #ffffff;
  position: absolute;
  bottom: 40px;
  font-weight: 900;
}

@media only screen and (min-width: 768px) {
  .container-content .block-content__main-content_text-abs {
    font-size: 4.3rem;
  }
}

.container-content .block-content__main-content_main-title {
  font-size: 1.4rem;
  font-weight: 600;
  text-transform: uppercase;
  padding-bottom: 15px;
  margin-bottom: 15px;
  position: relative;
  line-height: 2rem;
}

.container-content .block-content__main-content_main-title::before {
  content: '';
  width: 10%;
  height: 3px;
  background: #a1c856;
  position: absolute;
  bottom: 0;
  left: 0;
}

@media only screen and (min-width: 768px) {
  .container-content .block-content__main-content_main-title {
    font-size: 2.0rem;
    margin-bottom: 30px;
  }
}

.container-content .block-content__main-content_text img {
  display: block;
}

.container-content .block-content__main-content_text img:nth-of-type(odd) {
  width: 100%;
  margin: 0 auto 15px;
}

@media only screen and (min-width: 600px) {
  .container-content .block-content__main-content_text img:nth-of-type(odd) {
    float: left;
    padding-right: 25px;
    margin-left: -20px;
    width: 50%;
  }
}

.container-content .block-content__main-content_text img:nth-of-type(even) {
  float: right;
  width: 50%;
}

@media only screen and (min-width: 600px) {
  .container-content .block-content__main-content_text img:nth-of-type(even) {
    float: left;
    padding-left: 25px;
    margin-right: -20px;
  }
}

.container-content .block-content__main-content_text p {
  font-size: 1.2rem;
  margin-bottom: 15px;
}

.container-content .block-content__main-content_text p:last-child {
  margin-bottom: 65px;
}

.container-content .block-content__main-content_text p a {
  color: #a1c856;
  text-decoration: underline;
}

.container-content .block-content__main-content_text p a:hover {
  text-decoration: none;
}

html {
  font-size: 62.5%;
  font-size-adjust: 100%;
}

body {
  color: #383838;
  font-family: 'Source Sans Pro','Helvetica Neue',Helvetica,Arial,sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 1.42857143;
  font-size: 1.6rem;
}
